<template>
  <div id="register">
    <div class="main">
    	<img class="login-logo" src="../../assets/home/logo.png"/>
      <div class="register-main">
      	<div class="title">
      		<span :class="{active:1 == tabIndex}" @click="changeType(1)">登录</span>
      		<span :class="{active:2 == tabIndex}" @click="changeType(2)">注册</span>
      	</div>
      	
      	<div class="login-wrap" v-show="tabIndex == 1">
      		<div class="login-mian" v-show="!showForget">
      			<input class="bb-ccc" type="tel" maxlength="11" placeholder="请输入手机号码" v-model="login.mobile"/>
      			<div class="psw-wrap">
	      			<input class="bb-ccc" type="password" placeholder="请输入登录密码" v-model="login.password" v-if="!showPsw"/>
              <input class="bb-ccc" type="text" placeholder="请输入登录密码" v-model="login.password" v-if="showPsw"/>
  	    			<img class="eye-img" src="../../assets/login_btn_eye_nor@2x.png" v-if="!showPsw" @click="changeShow"/>
    	  			<img class="eye-img" src="../../assets/login_btn_eye_pre@2x.png" v-if="showPsw" @click="changeShow"/>
      			</div>
      			<div class="forget-text" @click="resetPsw">忘记密码？</div>
      			<div class="login-btn btn" :class="{active:loginActive}" @click="loginSubmit">登录</div>
      		</div>
      		
      		<div class="login-forget" v-show="showForget">
      			<div v-show="!showReset">
	      			<input class="bb-ccc" v-model="forget.mobile" type="tel" maxlength="11" placeholder="请输入手机号码" @input="hidePhone"/>
	      			<div class="phone-code bb-ccc">
			          <input type="text" v-model="forget.code" maxlength="6" class="bb-ccc input-code" placeholder="请输入短信验证码">
			          <div class="get-code" :class="{'c-999': forget.count>0}" @click="forgetCode">{{forget.count>0?'('+forget.count+'s)':'获取验证码'}}</div>
		        		<p class="remind" v-if="forget.isSend">验证码已发送至手机：{{forget.mobile}}</p>
			        </div>
			        <div class="forget-btn btn" :class="{active:forgetActive}" @click="nextStep">下一步</div>
      			</div>
      			<div v-show="showReset">
	      			<input class="bb-ccc" type="password" placeholder="请设置密码（6-16位字母和数字组合）" v-model="forget.password"/>
      				<div class="btn" :class="{active:finishActive}" @click="finish">完成</div>
      			</div>
      		</div>
      	</div>
      	
      	<div v-show="tabIndex == 2" style="margin-top: 0.4rem;">
	        <input type="tel" v-model="register.mobile"  maxlength="11" class="bb-ccc" placeholder="请输入手机号码" @input="hidePhone">
	        <div class="phone-code bb-ccc">
	          <input type="text" v-model="register.code" class="bb-ccc input-code" placeholder="请输入短信验证码">
	          <div class="get-code" :class="{'c-999': register.count>0}" @click="phoneCode">{{register.count>0?'('+register.count+'s)':'获取验证码'}}</div>
	        </div>
	        <input type="password" v-model="register.password"  class="bb-ccc" placeholder="请设置密码（6-16位字母和数字组合）">
	        <p class="remind" v-if="register.isSend">验证码已发送至手机：{{register.mobile}}</p>
	        <div class="btn" :class="{active:registerActive}"  @click="submitSmsCode">下一步</div>
	        <p class="agreement">已阅读并同意<span>《礼物飞注册协议》</span></p>
      	</div>
      </div>
    </div>
  </div>
</template>

<script>
  import md5 from 'blueimp-md5';
	
  export default {
    data () {
      return {
        psw:'',
        tabIndex:1,
        showPsw:false,
        showForget:false,
        showReset:false,
        login:{
        	mobile:'',
        	password:'',
        },
        forget:{
        	mobile: '',
        	code: '',
        	count: 0,
        	isSend:false,
        	password:'',
        },
        register:{
        	mobile: '',
        	code: '',
        	password: '',
        	isSend: false,
        	count: 0,
        	checkKey:'',
        }
      }
    },
    watch: {},
    computed:{
    	loginActive(){
    		if(this.login.mobile.length == 11 && this.login.password.length >=6){
    			return true
    		}else{
    			return false
    		}
    	},
    	forgetActive(){
    		if(this.forget.mobile.length == 11 && this.forget.code.length > 0){
    			return true
    		}else{
    			return false
    		}
    	},
    	registerActive(){
    		let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
    		console.log(reg.test(this.register.password))
    		if(reg.test(this.register.password) && this.register.mobile.length == 11 && this.register.code.length >=6){
    			return true
    		}else{
    			return false
    		}
    	},
    	finishActive(){
    		let reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
    		console.log(reg.test(this.forget.password))
    		if(reg.test(this.forget.password)){
    			return true
    		}else{
    			return false
    		}
    	},
    },
    methods: {
    	//切换注册登录
    	changeType(opt){
    		this.tabIndex = opt
    		this.showForget = false
    		
    	},
    	//是否显示密码
    	changeShow(){
    		this.showPsw = !this.showPsw
    	},
    	//忘记密码
    	resetPsw(){
    		this.showForget = true
    	},
    	nextStep(){
    		if(this.forgetActive){
    			this.submitForgetCode()
    		}
    	},
    	finish(){
    		this.getUserResetPassword()
    	},
			//登录
			loginSubmit(){
				let params = {
					a:1,
					name:this.login.mobile,
					password:md5(this.login.password),
				}
				this.$Api.UserLogin(params).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.$router.replace({name:'Index'})
					}else{
						this.$toast('登录失败')
					}
				})
			},
    	
    	
    	//忘记
    	forgetCode(){
    		if(this.forget.mobile.length==11){
    			console.log(this.forget.count)
	        if(this.forget.count>0){
	        	return;
	        }else{
		        this.forget.count = 60;
		        this.forget.isSend = true
		        this.forgetCountDown()
	          this.getForgetCode()
	        }
      	}else{
      		this.$toast('请输入正确手机号')
      	}
    	},
      phoneCode(){
      	//注册验证码
      	if(this.register.mobile.length==11){
  			console.log(this.register.count)
	        if(this.register.count>0){
	        	return;
	        }else{
	        	this.register.count = 60;
	        	this.register.isSend = true
	        	this.countDown()
	        	this.getSMSCode()
	        }
      	}else{
      		this.$toast('请输入正确手机号')
      	}
      },
      forgetCountDown: function(){
        let $this = this;
        var cd = setInterval(function() {
          $this.forget.count--;
          if($this.forget.count <= 0){
            $this.forget.count = 0;
            clearInterval(cd);
          }
        },1000)
      },
      
      countDown: function(){
        let $this = this;
        var cd = setInterval(function() {
          $this.register.count--;
          if($this.register.count <= 0){
            $this.register.count = 0;
            clearInterval(cd);
          }
        },1000)
      },
      getUserResetPassword(){
    		let user = {
    			name:this.forget.mobile,
    			password : md5(this.forget.password),
    		}
    		let checkKey = this.forget.checkKey
				this.$Api.UserResetPassword(user,checkKey).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
    				this.$popup({
    					showCancel:false,
    					sureText : '确定',
    					title:'设置成功',
    					content:'新密码设置成功,请重新登录',
    				}).then((res)=>{
    					if(res == 'sure'){
    						this.showForget = false
    						this.showReset = false
    						
    						this.forget = {
				        	mobile: '',
				        	code: '',
				        	count: 0,
				        	isSend:false,
				        	password:'',
				        }
    					}
    				})
					}else{
						this.$toast('重置失败')
					}
				})
    	},
    	hidePhone(){
    		this.register.isSend = false
    		this.forget.isSend = false
    	},
    	//获取验证码
      getSMSCode() {
        let param = {
          a: 1,
          type: 1,
          mobile: this.register.mobile,
        }

        this.$Api.SMSCode(param).then((res) => {
          console.log(res);
          if(res.q.s == 0) {
//          this.sended = true;
            this.$toast('发送成功!');
            this.register.checkKey = res.q.checkKey;
//          for(let i = count; i >= 0; i--) {
//            setTimeout(() => {
//              this.btnText = i + 's';
//              if(i == 0) {
//                this.btnText = '获取验证码'
//                this.sended = false;
//              }
//            }, (count - i - 1) * 1000)
//          }
          }else {
            this.$toast(res.q.d);
          }
        })
      },
    	//获取验证码
      getForgetCode() {
        let param = {
          a: 1,
          type: 2,
          mobile: this.forget.mobile,
        }

        this.$Api.SMSCode(param).then((res) => {
          console.log(res);
          if(res.q.s == 0) {
            this.$toast('发送成功!');
            this.forget.checkKey = res.q.checkKey;
          }else {
            this.$toast(res.q.d);
          }
        })
      },
      //提交注册验证码
      submitSmsCode() {
        let codeParam = {
          a: 2,
          type: 1,
          mobile: this.register.mobile,
          checkKey:this.register.checkKey,
          code: this.register.code
        }

        this.$Api.SMSCode(codeParam).then((res) => {
          console.log(res)
          if(res.q.s == 0) {
          	this.$router.push({name:'ImproveInformation',params:{psw:this.register.password}})
          }else {
            this.$toast('手机验证码出错')
          }
        })
      },
      //提交忘记密码验证码
      submitForgetCode() {
        let codeParam = {
          a: 2,
          type: 2,
          mobile: this.forget.mobile,
          checkKey:this.forget.checkKey,
          code: this.forget.code
        }

        this.$Api.SMSCode(codeParam).then((res) => {
          console.log(res)
          if(res.q.s == 0) {
          	this.showReset = true
          }else {
            this.$toast('手机验证码出错')
          }
        })
      },
    	
    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #register{
    height: 100%;
    background: #fff;
    width: 100%;
    .main{
      .login-logo{
        width: 100%;
    		height: 7.2rem;
      }
      /*height: 100%;
      width: 100%;
      background: url("../../assets/home/logo.png") no-repeat center top;
      background-size: 100% auto;*/
    }
    .register-main{
      /*width: 8rem;*/
      width: 80%;
      /*margin: 0 auto;*/
      /*position: absolute;*/
      /*margin:7.07rem auto 0;*/
     /* left: 50%;
      transform: translateX(-50%);*/
      margin:0 auto;
      height: 8.43rem;
      padding: 0.44rem;
      
      box-shadow: 0rem -0.07rem 0.13rem 0.01rem rgba(67, 67, 67, 0.06), 0rem 0.07rem 0.13rem 0rem rgba(0, 0, 0, 0.13);
      border-radius: 0.11rem;

      h3{
        text-align: center;
        font-size: 0.48rem;
        color: #de7fef;
        margin-bottom: 0.25rem;
      }
      .phone-code,
      input{
        width: 100%;
        height: 1.2rem;
        border: none;
        font-size: 0.37rem;
        color: #333;
        background: #fff;
        position: relative;
      }
      .bb-ccc{
        border-bottom: 1px #d8d8d8 solid;
      }
      input.input-code{
        width: 4.5rem;
      }
      .get-code{
        width: 2.5rem;
        height: 0.6rem;
        line-height: 0.6rem;
        text-align: center;
        font-size: 0.35rem;
        color: #de7fef;
        position: absolute;
        right: 0;
        top: 0.3rem;
        border-left: 1px #d8d8d8 solid;
      }
      .c-999{
        color: #999;
      }
      .title{
        font-size: 0;
        >span{
          display: inline-block;
          font-size: 0.48rem;
          width: 50%;
          text-align: center;
          color: #000;
          &:first-child{
            border-right:1px solid #dcdcdc;
          }
        }
        .active{
          color: #de7fef;
        }

      }
      .login-wrap{

        .login-mian{
          margin-top: 0.4rem;
          input{
            width: 100%;
            height: 1.2rem;
            border: none;
            font-size: 0.37rem;
            color: #333;
            background: #fff;
            position: relative;
            border-bottom: 1px #d8d8d8 solid
          }
          .eye-img{
            position: absolute;
            width: 0.45rem;
            height: 0.4rem;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
          }
          .psw-wrap{
            position: relative;

          }
          .forget-text{
            font-size: 0.32rem;
            margin-top: 0.32rem;
            text-align: right;
            color: #666;

          }
          .login-btn{
            font-size: 0.43rem;
            width: 100%;
            height: 1.07rem;
            line-height: 1.07rem;
            border-radius: 0.53rem;
            margin-top: 0.53rem;
            text-align: center;
            color: #fff;
            background: #ccc;
          }
        }
        .login-forget{
          margin-top: 0.4rem;
          input{
            width: 100%;
            height: 1.2rem;
            border: none;
            font-size: 0.37rem;
            color: #333;
            background: #fff;
            position: relative;
            border-bottom: 1px #d8d8d8 solid
          }
          .input-code{
            width: 4.5rem
          }
        }
        .btn{
          font-size: 0.43rem;
            width: 100%;
            height: 1.07rem;
            line-height: 1.07rem;
            border-radius: 0.53rem;
            margin-top: 1.17rem;
            text-align: center;
            color: #fff;
            background: #ccc;
        }
        .btn.active{
          background: linear-gradient(135deg,#fe9bba 0%,#de7fef 100%);
        }
      }






    }
    .remind{
      font-size: 0.32rem;
      color: #666;
      margin-top: 0.3rem;

    }
    .btn{
      /*width: 6.93rem;*/
      width: 100%;
      height: 1.07rem;
      /*background-image: linear-gradient(135deg,
      #fe9bba 0%,
      #de7fef 100%);*/
     	background: #ccc;
      border-radius: 0.53rem;
      margin: 0 auto;
      line-height: 1.07rem;
      color: #fff;
      font-size: 0.43rem;
      text-align: center;
      margin-top: 0.53rem;
    }
    .btn.active{
      background: linear-gradient(135deg,#fe9bba 0%,#de7fef 100%);
    }
    .agreement{
      font-size: 0.32rem;
      color: #666666;
      margin-top: 0.4rem;

      span{
        color: #de7fef;
      }
    }
  }

</style>



